.section-cooperative {
    @extend %site-section;
    &__card-wrap {
        perspective: 1000px;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 2.4rem;
        @include susy-breakpoint($s-320, $c8) {
            grid-template-columns: 1fr;
            gap: 2.4rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            grid-template-columns: repeat(2, 1fr);
        }
    }
}

.section-cooperative-card {
    transform-style: preserve-3d;
    display: flex;
    flex-direction: column;
    background-color: var(--color-yellow-100);
    border-radius: 3.2rem;
    padding: 4rem 0;
    gap: 2.4rem;
    @include susy-breakpoint($s-320, $c8) {
        border-radius: 2.4rem;
        padding-top: 2.4rem;
        padding-bottom: 2.4rem;
        gap: 1.6rem;
    }
    @include susy-breakpoint($s-768, $c12) {
        border-radius: 2.4rem;
        padding-top: 2.4rem;
        padding-bottom: 2.4rem;
        gap: 1.6rem;
    }
    @include susy-breakpoint($s-960, $c12) {
        border-radius: 2.4rem;
        padding-top: 2.4rem;
        padding-bottom: 2.4rem;
    }
    @include susy-breakpoint($s-1201, $c18) {
        border-radius: 2.4rem;
    }
    &__img {
        width: 24rem;
        height: 24rem;
        margin: 0 auto;
        img {
            max-width: 100%;
            display: block;
            margin: 0;
            border-radius: 50%;
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 14rem;
            height: 14rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            width: 14rem;
            height: 14rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 14rem;
            height: 14rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            width: 16rem;
            height: 16rem;
        }
    }
    &__name {
        color: var(--color-red-500);
        font-size: var(--font-size-xl);
        text-align: center;
        line-height: 1.6;
        border-bottom: 0.1rem dashed var(--color-grey-100);
        padding-bottom: 1.6rem;
        @include susy-breakpoint($s-320, $c8) {
            font-size: var(--font-size-lg);
            padding-bottom: 0.8rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            font-size: var(--font-size-lg);
            padding-left: 2rem;
            padding-right: 2rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: var(--font-size-lg);
            padding-left: 2rem;
            padding-right: 2rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            font-size: var(--font-size-lg);
        }
    }
    &__counter {
        border-bottom: 0.1rem dashed var(--color-grey-100);
        padding-bottom: 1.6rem;
        h4 {
            color: var(--color-grey-400);
            font-size: var(--font-size-base);
            text-align: center;
            margin-bottom: 1.6rem;
        }
        p {
            @extend %gradient-bg;
            font-size: var(--font-size-6xl);
            text-align: center;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
        }
    }
    &__intro {
        h4 {
            color: var(--color-grey-400);
            font-size: var(--font-size-base);
            text-align: center;
            margin-bottom: 1.6rem;
        }
        p {
            font-size: var(--font-size-sm);
            color: var(--color-grey-400);
            line-height: 1.6;
            padding: 0 4rem;
            @include susy-breakpoint($s-320, $c8) {
                padding-left: 2rem;
                padding-right: 2rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                padding-left: 2rem;
                padding-right: 2rem;
            }
        }
    }
}