.section-kv {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    position: relative;
    height: 100vh;
    height: calc(var(--vh, 1vh) * 100);
    background-color: #333;
    overflow: hidden;
    &__main-title {
        position: absolute;
        left: 50%;
        top: 16rem;
        transform: translate3d(-50%,0,0);
        font-size: var(--font-size-4xl);
        color: #fff;
        font-weight: 500;
        span {
            padding: 0 0.8rem;
            font-weight: 300;
        }
        @include susy-breakpoint($s-320, $c8) {
            font-size: var(--font-size-2xl);
            top: 8rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            font-size: var(--font-size-2xl);
            top: 10rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: var(--font-size-2xl);
            top: 10rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            font-size: var(--font-size-3xl);
            top: 10rem;
        }
    }
    &__weekly-calendar {
        font-size: var(--font-size-3xl);
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(-50%,-50%,0);
        color: #fff;
        line-height: 1.1;
        font-weight: 900;
        width: 100%;
        @include susy-breakpoint($s-320, $c8) {
            transform: translate3d(-50%,-340%,0);
        }
        span {
            display: block;
            text-align: center;
            &:nth-child(1) {
                font-size: var(--font-size-7xl);
                letter-spacing: 0.8rem;
                text-indent: 0.8rem;
                @include susy-breakpoint($s-320, $c8) {
                    font-size: var(--font-size-4xl);
                }
                @include susy-breakpoint($s-768, $c12) {
                    font-size: var(--font-size-5xl);
                }
                @include susy-breakpoint($s-960, $c12) {
                    font-size: var(--font-size-5xl);
                }
            }
            &:nth-child(2) {
                font-size: var(--font-size-4xl);
                @include susy-breakpoint($s-320, $c8) {
                    font-size: var(--font-size-2xl);
                }
                @include susy-breakpoint($s-768, $c12) {
                    font-size: var(--font-size-2xl);
                }
                @include susy-breakpoint($s-960, $c12) {
                    font-size: var(--font-size-3xl);
                }
            }
        }
    }
    &__chi,
    &__pet {
        display: flex;
        justify-content: center;
        align-items: center;
        img {
            width: 16rem;
            height: 16rem;
            display: block;
            margin: 0 auto 0.8rem;
            animation-name: flipAnimation;
            animation-duration: 1000ms;
            animation-timing-function: ease-in-out;
            animation-iteration-count: infinite;
            animation-direction: alternate;
            @include susy-breakpoint($s-320, $c8) {
                width: 8rem;
                height: 8rem;
            }
            @include susy-breakpoint($s-768, $c12) {
                width: 8rem;
                height: 8rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                width: 12rem;
                height: 12rem;
            }
            @include susy-breakpoint($s-1201, $c18) {
                width: 12rem;
                height: 12rem;
            }
        }
        p {
            font-size: var(--font-size-2xl);
            text-align: center;
            line-height: 1.6;
            color: #fff;
            font-weight: 500;
            @include susy-breakpoint($s-960, $c12) {
                font-size: var(--font-size-xl);
            }
        }
    }
    &__chi {
        background-color: var(--color-chi-500);
    }
    &__pet {
        background-color: var(--color-pet-500);
    }
    &__wave {
        background: transparent url('../images/wave.svg') no-repeat 0 0; 
        position: absolute;
        bottom: -4px;
        width: 6400px;
        height: 100px;
        animation: wave 14s linear infinite;
        transform: translate3d(0, 0, 0);
    }
    &__btn-scroll {
        @extend %gradient-bg;
        width: 4rem;
        height: 4rem;
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate3d(-50%,-300%,0);
        border-radius: 50%;
        border: 0;
        &::before {
            width: 0.8rem;
            height: 0.8rem;
            border-right: solid 0.2rem #fff;
            border-bottom: solid 0.2rem #fff;
            content: "";
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate3d(-50%,-70%,0) rotate(45deg);
        }
    }
}

@keyframes wave {
    0% {
        transform: translate3d(0, 0, 0);
    }
    100% {
        transform: translate3d(-50%, 0, 0);
    }
}

@keyframes flipAnimation {
    0% {
        transform: rotate(10deg);
    }
    100% {
        transform: rotate(-10deg);
    }
}


@keyframes swell {
    0%, 100% {
        transform: translate3d(0,-25px,0);
    }
    50% {
        transform: translate3d(0,5px,0);
    }
}