.section-origin {
    @extend %site-section;
    &__block {
        display: flex;
        gap: 16rem;
        align-items: center;
        &:not(:last-child) {
            margin-bottom: 8rem;
        }
        &:nth-child(2) {
            flex-direction: row-reverse;
            @include susy-breakpoint($s-320, $c8) {
                flex-direction: column;
            }
        }
        @include susy-breakpoint($s-320, $c8) {
            flex-direction: column;
            gap: 4rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            gap: 4rem;
            align-items: flex-start;
        }
        @include susy-breakpoint($s-960, $c12) {
            gap: 5.6rem;
            align-items: flex-start;
        }
        @include susy-breakpoint($s-1201, $c18) {
            gap: 8rem;
        }
    }
    &__img {
        flex: 0 0 38rem;
        img {
            max-width: 100%;
            display: block;
            border-radius: 50%;
        }
        @include susy-breakpoint($s-320, $c8) {
            flex: 0;
            width: 20rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            flex: 0 0 20rem;
        }
    }
    &__content {
        flex: 1;
        & > h3 {
            margin-bottom: 2.4rem;
            font-size: var(--font-size-2xl);
            color: var(--color-grey-700);
            @include susy-breakpoint($s-320, $c8) {
                font-size: var(--font-size-xl);
                text-align: center;
            }
            @include susy-breakpoint($s-768, $c12) {
                font-size: var(--font-size-xl);
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: var(--font-size-xl);
            }
        }
    }
    &__detail {
        display: flex;
        flex-direction: column;
        gap: 2rem;
        & > p {
            font-size: var(--font-size-xl);
            color: var(--color-grey-400);
            line-height: 1.6;
            &.highlight {
                color: var(--color-red-500);
                font-weight: 900;
            }
            @include susy-breakpoint($s-320, $c8) {
                font-size: var(--font-size-sm);
            }
            @include susy-breakpoint($s-768, $c12) {
                font-size: var(--font-size-sm);
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: var(--font-size-base);
            }
            @include susy-breakpoint($s-1201, $c18) {
                font-size: var(--font-size-lg);
            }
        }
    }
}