.section-order {
    @extend %site-section;
    &__form {
        width: 56rem;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        gap: 3.2rem;
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
            gap: 2.4rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            width: 44rem;
            gap: 2.4rem;
        }
    }
    &__label {
        font-size: var(--font-size-base);
        color: var(--color-grey-400);
        margin-bottom: 1.2rem;
        display: block;
        @include susy-breakpoint($s-320, $c8) {
            font-size: var(--font-size-sm);
        }
    }
    &__select-wrap {
        @extend %site-select;
    }
    &__input {
        @extend %site-input;
    }
    &__counter {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__control {
        @extend %counter-wrap;
    }
    &__btn-minus,
    &__btn-plus {
        @extend %counter-btn;
    }
    &__input-counter {
        @extend %site-input;
        width: 20rem;
        text-align: center;
        @include susy-breakpoint($s-320, $c8) {
            width: 9.6rem;
        }
    }
    &__total {
        font-size: var(--font-size-xl);
        color: var(--color-red-500);
        @include susy-breakpoint($s-320, $c8) {
            font-size: var(--font-size-base);
        }
    }
    &__inner-row {
        @extend %site-address;
    }
    &__btn-submit {
        @extend %gradient-bg;
        width: 100%;
        border: 0;
        border-radius: 3.6rem;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 1.6rem;
        font-size: var(--font-size-xl);
        color: #fff;
        @include susy-breakpoint($s-320, $c8) {
            font-size: var(--font-size-lg);
            padding: 1.2rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            font-size: var(--font-size-lg);
            padding: 1.2rem;
        }
    }
}