.section-partner {
    @extend %site-section;
    &__wrap {
        width: 80rem;
        display: flex;
        gap: 2rem;
        flex-wrap: wrap;
        justify-content: center;
        margin: 0 auto;
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
            padding: 0 2rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            width: 100%;
            padding: 0 2rem;
        }
    }
    &__btn-link {
        font-size: var(--font-size-xl);
        padding: 1.6rem 2.4rem;
        border: solid 0.2rem var(--color-pet-500);
        border-radius: 4rem;
        color: var(--color-pet-500);
        @include susy-breakpoint($s-320, $c8) {
            font-size: var(--font-size-sm);
            padding: 1.2rem 1.6rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            font-size: var(--font-size-sm);
            padding: 1.2rem 1.6rem;
        }
    }
}