.section-please-note {
    @extend %site-section;
    &__ol-list {
        display: flex;
        flex-direction: column;
        gap: 3.2rem;
        counter-reset: ol-list;
        @include susy-breakpoint($s-320, $c8) {
            gap: 1.6rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            gap: 1.6rem;
        }
        li {
            position: relative;
            font-size: var(--font-size-lg);
            color: var(--color-grey-400);
            line-height: 1.6;
            padding-left: 4.8rem;
            counter-increment: ol-list;
            @include susy-breakpoint($s-320, $c8) {
                font-size: var(--font-size-sm);
                padding-left: 3.2rem;
            }
            @include susy-breakpoint($s-768, $c12) {
                font-size: var(--font-size-sm);
                padding-left: 3.2rem;
            }
            a {
                color: var(--color-red-500);
            }
            &::before {
                @extend %gradient-bg;
                color: #fff;
                display: flex;
                justify-content: center;
                align-items: center;
                position: absolute;
                left: 0;
                top: -0.7rem;
                content: counter(ol-list);
                font-size: var(--font-size-lg);
                width: 4rem;
                height: 4rem;
                border-radius: 50%;
                @include susy-breakpoint($s-320, $c8) {
                    width: 2.4rem;
                    height: 2.4rem;
                    font-size: var(--font-size-sm);
                    top: -0.2rem;
                }
                @include susy-breakpoint($s-768, $c12) {
                    width: 2.4rem;
                    height: 2.4rem;
                    font-size: var(--font-size-sm);
                    top: -0.2rem;
                }
            }
        }
    }
}