:root {
    font-size: 10px;
    --color-bg: #F0EEE9;
    --color-yellow-100: #FFFDF8;
    --color-chi-500: #87B794;
    --color-pet-500: #9C95C1;
    --color-orange-500: #CDA96F;
    --color-red-500: #EA8F8F;
    --color-grey-100: #eeeeee;
    --color-grey-400: #666466;
    --color-grey-700: #333233;
    --font-size-7xl: 9.6rem;
    --font-size-6xl: 6.4rem;
    --font-size-5xl: 4.8rem;
    --font-size-4xl: 3.6rem;
    --font-size-3xl: 3rem;
    --font-size-2xl: 2.4rem;
    --font-size-xl: 2rem;
    --font-size-lg: 1.8rem;
    --font-size-base: 1.6rem;
    --font-size-sm: 1.4rem;
    --font-size-xs: 1.2rem;
    --border-radius-16: 1.6rem;
    --border-radius-8: 0.8rem;
    --element-margin-bottom: 1.6rem;
}

html {
    font-family: 'Roboto', 'Noto Sans TC', "微軟正黑體", "Microsoft JhengHei", "Arial Unicode MS", "Helvetica Neue", Helvetica, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body {
    background-color: var(--color-bg);
}

.site-nav-toggle {
    @extend %gradient-bg;
    border-radius: 50%;
    width: 4rem;
    height: 4rem;
    display: none;
    position: fixed;
    right: 2rem;
    top: 2rem;
    z-index: 200;
    border: 0;
    padding: 0;
    font-size: 0;
    transition: all 300ms ease;
    @include susy-breakpoint($s-320, $c8) {
        display: block;
    }
    &::before,
    &::after {
        background-color: #fff;
        content: "";
        width: 1.6rem;
        height: 0.2rem;
        border-radius: 0.2rem;
        position: absolute;
        left: 50%;
        top: 50%;
        transform-origin: 50% 50%;
        transition: transform 500ms ease;
    }
    &::before {
        transform: translate3d(-50%,-0.4rem,0);
    }
    &::after {
        transform: translate3d(-50%,0.2rem,0);
    }
    &.active {
        background: linear-gradient(180deg, #fff 0%, #fff 100%);
        &::before {
            background-color: var(--color-red-500);
            transform: translate3d(-50%,0,0);
        }
        &::after {
            background-color: var(--color-red-500);
            transform: translate3d(-50%,0,0);
        }
    }
}

.site-nav {
    justify-content: center;
    gap: 4rem;
    display: flex;
    background: linear-gradient(180deg, #F5BD89 0%, #EA8F8F 100%);
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 100;
    transform: translate3d(0,0,0);
    padding: 2rem 0;
    &__btn-section {
        color: #fff;
        font-size: var(--font-size-lg);
        line-height: 1;
        white-space: nowrap;
        opacity: 0.7;
        transition: opacity 300ms ease;
        font-weight: 900;
        &:hover {
            opacity: 1;
        }
        &.active {
            opacity: 1;
        }
        @include susy-breakpoint($s-320, $c8) {
            font-size: var(--font-size-base);
        }
        @include susy-breakpoint($s-768, $c12) {
            font-size: var(--font-size-base);
        }
    }
    @include susy-breakpoint($s-320, $c8) {
        position: fixed;
        flex-direction: column;
        border-radius: 0;
        width: 100%;
        height: 100vh;
        padding: 0;
        top: 0;
        left: 0;
        align-items: center;
        gap: 3.2rem;
        transition: transform 300ms ease-in-out;
        transform: translate3d(100%,0,0);
        &.active {
            transform: translate3d(0,0,0);
        }
    }
}

.site-main {
    display: flex;
    flex-direction: column;
    gap: 20rem;
    padding-bottom: 20rem;
    @include susy-breakpoint($s-320, $c8) {
        gap: 8rem;
        padding-bottom: 6.4rem;
    }
    @include susy-breakpoint($s-768, $c12) {
        gap: 8rem;
        padding-bottom: 6.4rem;
    }
    @include susy-breakpoint($s-960, $c12) {
        gap: 12rem;
        padding-bottom: 12rem;
    }
}

.section-title {
    display: flex;
    flex-direction: column;
    margin-bottom: 5.6rem;
    @include susy-breakpoint($s-320, $c8) {
        margin-bottom: 4rem;
    }
    @include susy-breakpoint($s-768, $c12) {
        margin-bottom: 4rem;
    }
    @include susy-breakpoint($s-960, $c12) {
        margin-bottom: 4rem;
    }
    span {
        &:nth-child(1) {
            text-align: center;
            font-size: var(--font-size-4xl);
            background: linear-gradient(180deg, #F5BD89 0%, #EA8F8F 100%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            margin-bottom: 0.8rem;
            position: relative;
            &::before,
            &::after {
                content: "";
                position: absolute;
                left: 50%;
                width: 0.4rem;
                height: 3.2rem;
                border-radius: 0.4rem;
                transition-duration: 50% 100%;
                top: 0;
                background: linear-gradient(180deg, #F5BD89 0%, #EA8F8F 100%);
                @include susy-breakpoint($s-320, $c8) {
                    height: 2rem;
                }
                @include susy-breakpoint($s-768, $c12) {
                    height: 2rem;
                }
                @include susy-breakpoint($s-960, $c12) {
                    height: 2.4rem;
                }
            }
            &::before {
                transform: translate3d(-9rem,0.2rem,0) rotate(-30deg);
                @include susy-breakpoint($s-320, $c8) {
                    transform: translate3d(-6rem,0.2rem,0) rotate(-30deg);
                }
                @include susy-breakpoint($s-768, $c12) {
                    transform: translate3d(-6rem,0.2rem,0) rotate(-30deg);
                }
                @include susy-breakpoint($s-960, $c12) {
                    transform: translate3d(-7.2rem,0.2rem,0) rotate(-30deg);
                }
            }
            &::after {
                transform: translate3d(8.6rem,0.2rem,0) rotate(30deg);
                @include susy-breakpoint($s-320, $c8) {
                    transform: translate3d(5.6rem,0.2rem,0) rotate(30deg);
                }
                @include susy-breakpoint($s-768, $c12) {
                    transform: translate3d(5.6rem,0.2rem,0) rotate(30deg);
                }
                @include susy-breakpoint($s-960, $c12) {
                    transform: translate3d(7rem,0.2rem,0) rotate(30deg);
                }
            }
            @include susy-breakpoint($s-320, $c8) {
                font-size: var(--font-size-2xl);
            }
            @include susy-breakpoint($s-768, $c12) {
                font-size: var(--font-size-2xl);
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: var(--font-size-3xl);
            }
        }
        &:nth-child(2) {
            font-size: var(--font-size-2xl);
            text-align: center;
            color: var(--color-red-500);
            text-transform: uppercase;
            @include susy-breakpoint($s-320, $c8) {
                font-size: var(--font-size-lg);
            }
            @include susy-breakpoint($s-768, $c12) {
                font-size: var(--font-size-lg);
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: var(--font-size-xl);
            }
        }
    }
}

.section-inner {
    margin: 0 auto;
    width: 192rem;
    box-sizing: border-box;
    padding: 0 12rem;
    @include susy-breakpoint($s-320, $c8) {
        width: 100%;
        padding-left: 2rem;
        padding-right: 2rem;
    }
    @include susy-breakpoint($s-768, $c12) {
        width: 70rem;
        padding-left: 2rem;
        padding-right: 2rem;
    }
    @include susy-breakpoint($s-960, $c12) {
        width: 88rem;
        padding-left: 2rem;
        padding-right: 2rem;
    }
    @include susy-breakpoint($s-1201, $c18) {
        width: 112rem;
        padding-left: 2rem;
        padding-right: 2rem;
    }
    @include susy-breakpoint($s-1601, $c18) {
        width: 148rem;
        padding-left: 2rem;
        padding-right: 2rem;
    }
    &.full {
        width: 100%;
        padding-left: 0;
        padding-right: 0;
    }
}

.site-footer {
    position: relative;
    overflow: hidden;
    padding: 12rem 0 4rem;
    background: linear-gradient(180deg, #F5BD89 0%, #EA8F8F 100%);
    display: flex;
    flex-direction: column;
    gap: 1.6rem;
    @include susy-breakpoint($s-320, $c8) {
        gap: 1.2rem;
    }
    @include susy-breakpoint($s-768, $c12) {
        gap: 1.2rem;
    }
    &__wave {
        background: url('../images/footer-wave.svg') no-repeat 0 0; 
        position: absolute;
        width: 640rem;
        height: 10rem;
        animation: waveFooter 15s linear infinite;
        transform: translate3d(0, 0, 0);
        z-index: 20;
        top: 0;
    }
    &__logo {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 2rem;
        img {
            display: block;
            height: 3rem;
        }
    }
    &__contact {
        display: flex;
        justify-content: center;
        gap: 0.8rem;
        span {
            font-size: var(--font-size-sm);
            color: #fff;
            @include susy-breakpoint($s-320, $c8) {
                font-size: var(--font-size-xs);
            }
            @include susy-breakpoint($s-768, $c12) {
                font-size: var(--font-size-xs);
            }
        }
    }
    &__copyright {
        font-size: var(--font-size-sm);
        color: #fff;
        text-align: center;
        @include susy-breakpoint($s-320, $c8) {
            font-size: var(--font-size-xs);
        }
        @include susy-breakpoint($s-768, $c12) {
            font-size: var(--font-size-xs);
        }
    }
}

@keyframes waveFooter {
    0% {
        transform: translate3d(0, 0, 0);
    }
    100% {
        transform: translate3d(-3200px, 0, 0);
    }
}

.btn-gotop {
    @extend %gradient-bg;
    width: 4rem;
    height: 4rem;
    position: fixed;
    right: 2rem;
    bottom: 2rem;
    transform: translate3d(0,0,0);
    border-radius: 50%;
    border: 0;
    font-size: 0;
    opacity: 0;
    transition: opacity 300ms ease;
    z-index: 300;
    &.active {
        opacity: 1;
    }
    &::before {
        width: 0.8rem;
        height: 0.8rem;
        border-right: solid 0.2rem #fff;
        border-bottom: solid 0.2rem #fff;
        content: "";
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(-50%,-50%,0) rotate(-135deg);
    }
}