.section-introduce {
    $self: &;
    @extend %site-section;
    &__item {
        display: flex;
        &:nth-child(2) {
            flex-direction: row-reverse;
            @include susy-breakpoint($s-320, $c8) {
                flex-direction: column;
            }
        }
        @include susy-breakpoint($s-320, $c8) {
            flex-direction: column;
        }
        &:nth-child(1) {
            #{$self}__img {
                background-color: var(--color-orange-500);
            }
        }
        &:nth-child(2) {
            #{$self}__img {
                background-color: var(--color-chi-500);
            }
        }
        &:nth-child(3) {
            #{$self}__img {
                background-color: var(--color-pet-500);
            }
        }
    }
    &__img {
        overflow: hidden;
        flex: 0 0 50%;
        img {
            max-width: 100%;
            display: block;
            margin: 0;
        }
    }
    &__content {
        flex: 0 0 50%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        &.all {
            background-color: var(--color-orange-500);
        }
        &.chi {
            background-color: var(--color-chi-500);
        }
        &.pet {
            background-color: var(--color-pet-500);
        }
        h3 {
            font-size: var(--font-size-2xl);
            color: #fff;
            margin-bottom: 1.6rem;
            @include susy-breakpoint($s-320, $c8) {
                font-size: var(--font-size-xl);
            }
            @include susy-breakpoint($s-768, $c12) {
                font-size: var(--font-size-xl);
            }
        }
        @include susy-breakpoint($s-320, $c8) {
            padding: 4rem 2rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            padding: 4rem 2rem;
        }
    }
    &__detail {
        width: 60rem;
        display: flex;
        flex-direction: column;
        gap: 0.8rem;
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
        }
        @include susy-breakpoint($s-768, $c12) {
            width: 100%;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 100%;
        }
        p {
            font-size: var(--font-size-sm);
            color: #fff;
            line-height: 1.6;
            @include susy-breakpoint($s-768, $c12) {
                font-size: var(--font-size-xs);
            }
            @include susy-breakpoint($s-960, $c12) {
                padding-left: 4rem;
                padding-right: 4rem;
            }
        }
    }
}